	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"html":"<p>It's amazing how much information you can get in real time about transportation world wide.</p>\n<p>in the air : <a href=\"https://flightradar24.com\" target=\"_blank\">https://flightradar24.com</a><br/>\non the sea : <a href=\"https://www.marinetraffic.com/\" target=\"_blank\">https://www.marinetraffic.com</a><br/>\nunderground in london : <a href=\"https://traintimes.org.uk/map/tube/\" target=\"_blank\">https://traintimes.org.uk/map/tube/</a><br/></p>","id":"/home/l/dev/gatsby/e4rache.github.io/src/pages/posts/01-transportnerd.md absPath of file >>> MarkdownRemark","frontmatter":{"path":"/transportnerd","title":"Transport Nerd","date":"2018-08-25"},"excerpt":"It's amazing how much information you can get in real time about transportation world wide. in the air :  https://flightradar24.com \non the…"}},{"node":{"html":"<p>I've been developing backend applications in Java for a long time and it was time for me to become a full stack developer so I decided to learn Javascript. Here is a compilation of resources that I used.</p>\n<p><a href=\"https://www.youtube.com/watch?v=0oXYLzuucwE&list=PL55RiY5tL51q4D-B63KBnygU6opNPFk_q\" target=\"_blank\">[academind] REST API with Node.js</a><br/>\n<a href=\"https://www.youtube.com/watch?v=Y0lDGjwRYKw&list=PL4cUxeGkcC9iK6Qhn-QLcXCXPQUov1U7f\" target=\"_blank\">[The Net Ninja] GraphQL Tutorial</a><br/>\n<a href=\"\" target=\"_blank\"></a><br/>\n<a href=\"\" target=\"_blank\"></a><br/></p>","id":"/home/l/dev/gatsby/e4rache.github.io/src/pages/posts/02-learning-javascript.md absPath of file >>> MarkdownRemark","frontmatter":{"path":"/learning-javascript","title":"Learning Javascript","date":"2018-08-26"},"excerpt":"I've been developing backend applications in Java for a long time and it was time for me to become a full stack developer so I decided to…"}},{"node":{"html":"<p>I've been a 6+ years Java developer.\nHere a the reasons one would want to make that switch.</p>\n<p>Java is full of boilerplate code which obscures the programmers intention\nSpring &#x26; Spring Boot teach a lesson: papering over complexity creates more complexity\nJava EE was a “design by committee” project that covers everything of everything in Enterprise application development, hence is exceedingly complex\nThe experience of Spring programming is it’s great until it isn’t, until that day an obscure impossible-to-understand Exception appears out of the depths of a subsystem you’ve never heard of requiring 3+ days just to figure out the problem\nWhat is the overhead required in the framework to allow coders to write zero code?\nWhile IDE’s like Eclipse are powerful, they are a symptom of Java’s complexity\nNode.js was the result of one guy honing and refining a vision of a lightweight event driven architecture, until Node.js revealed itself\nThe JavaScript community seems to appreciate removing boilerplate allowing the programmers intention to shine\nThe solution for Callback Hell, the async/await function, is an example of removing boilerplate so the programmers intention shines\nCoding with Node.js is a joy\nJavaScript lacks the strict type checking of Java, which is a blessing and a curse. Code is easier to write but requires more testing to ensure correctness\nThe npm/yarn package management system is excellent and joyful to use, versus the abomination that is Maven\nBoth Java and Node.js offer excellent performance, running counter to the myth that JavaScript is slow and therefore Node.js performance must be bad\nNode.js performance is riding the coattails of Google investment in V8 to speed up the Chrome browser\nThe intense competition between browsers is making JavaScript more and more powerful every year, benefitting Node.js</p>\n<p><a href=\"blog.sourcerer.io\">https://blog.sourcerer.io/why-is-a-java-guy-so-excited-about-node-js-and-javascript-7cfc423efb44</a></p>","id":"/home/l/dev/gatsby/e4rache.github.io/src/pages/posts/03-why-switch-from-java-to-javascript.md absPath of file >>> MarkdownRemark","frontmatter":{"path":"/switch-to-javascript","title":"Why I switched from Java to JavaScript / Node.js","date":"2018-08-28"},"excerpt":"I've been a 6+ years Java developer.\nHere a the reasons one would want to make that switch. Java is full of boilerplate code which obscures…"}}]}},"pathContext":{}}

